.header svg{
    position: relative;
    top: 10px;
}

.header h1{
    /* font-family: 'Inter' , sans-serif; */
    color: #1B1F2D;
    font-family: "SF Pro Display", sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 0.36px;
}

.form{
    margin-top: 50px;
    margin-bottom: 50px;
    padding-right: 20px;
    padding-left: 20px;
    margin-right: auto;
    margin-left: auto;
    width: 400px;
    /* background-color: gray; */
}

.form h2{
    color: #181818;
    text-align: center;
    font-family: "SF Pro Display", sans-serif;
    font-size: 28px;
    font-style: normal;
    font-weight: bold;
    line-height: normal;
}
.form h4{
    color: #181818;
    font-family: "SF Pro Display", sans-serif;
    font-size: 14px;
    font-style:normal;
    font-weight: 550;
}
.emailcontent , .passwordcontent{
    margin-top: -10px;
    margin-bottom: 10px;
    display: flex;
    width: 372px;
    padding: 11px 12px 12px 12px;
    align-items: center;
    gap: 10px;
    border-radius: 4px;
    border-color: transparent;
    background: var(--Gray-6, #F2F2F2);
}
.checkbox{
    border-radius: 5px;
    border: 1px solid var(--Gray-4, #BDBDBD);
    background: #FFF;
}
.form span{
    color: var(--Gray-2, #4F4F4F);
    font-family: "SF Pro Display", sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
.forgotPassword{
    background-color: transparent;
    border: none;
    
}
@media ( max-width:599px ){
    .forgotPassword{
        margin-bottom: 10px;
    }
}

.forgotPassword h3{
    color: var(--Blue-1, #2F80ED);
    font-family: "SF Pro Display", sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: 0.28px;
    cursor: pointer;
}
@media (max-width:500px) {
    .form{
        width: auto;
    }
    .emailcontent , .passwordcontent {
        width: 92%;
    }
}
.emailBtn{
    margin-bottom: 10px;
    width: 400px;
    border-radius: 6px;
    background: var(--Blue-1, #2F80ED);
    padding: 12px 18px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border: transparent;
    color: #FFF;
    font-family: "SF Pro Display", sans-serif;
    font-size: 15px;
    font-style: normal;
    transition: transform 1s ease-in-out;
}

.emailBtn:hover{
    border:  1px solid#2F80ED;
    background-color: white;
    color: #2F80ED;
    transform: scale(1.03);
    cursor: pointer;
}

.line{
    width: 100%;
    height: 1px;
    margin-top: 23px;
    background: #e0e0e0;
}
.textline{
    color: var(--Gray-2, #4F4F4F);
    text-align: center;
    font-family: "SF Pro Display", sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px; /* 128.571% */
    letter-spacing: 0.28px;
}

.googleBtn{
    margin-bottom: 10px;
    width: 400px;
    border-radius: 6px;
    border: 1px solid gray;
    background: #FFF;
    padding: 12px 18px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    color: var(--Gray-2, #4F4F4F);
    font-family: "SF Pro Display", sans-serif;
    font-size: 15px;
    font-style: normal;
    transition: transform 1s ease-in-out;
}
.googleBtn img{
    position: relative;
    top: 3px;
    margin-right: 7px;
    width: 20px;
    height: 20px;
}
.googleBtn:hover{
    border:  1px solid #4F4F4F;
    background-color: #4F4F4F;
    color: white;
    transform: scale(1.03);
    cursor: pointer;
}

.facebookBtn{
    margin-bottom: 10px;
    width: 400px;
    border-radius: 6px;
    border: 1px solid #475993;
    background: #475993;
    padding: 12px 18px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    color: white;
    font-family: "SF Pro Display", sans-serif;
    font-size: 15px;
    font-style: normal;
    transition: transform 1s ease-in-out;
}
.facebookBtn svg{
    position: relative;
    top: 3px;
    margin-right: 7px;
}
.facebookBtn:hover{
    border:  1px solid #475993;
    background-color: white;
    color: #475993;
    transform: scale(1.03);
    cursor: pointer;
}


@media (max-width:500px){
    .emailBtn{
        width: 98%;
    }
    .googleBtn{
        width: 98%;
    }
    .facebookBtn{
        width: 98%;
    }
}

.form h5{
    color: var(--Gray-2, #4F4F4F);
    text-align: center;
    font-family: 'SF Pro Display' ,sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.32px;
}
