.header svg{
    position: relative;
    top: 10px;
}

.header h1{
    /* font-family: 'Inter' , sans-serif; */
    color: #1B1F2D;
    font-family: "SF Pro Display", sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 0.36px;
}

.form{
    margin-top: 50px;
    margin-bottom: 50px;
    padding-right: 20px;
    padding-left: 20px;
    margin-right: auto;
    margin-left: auto;
    width: 440px;
    /* background-color: gray; */
}

.form h2{
    position: relative;
    top: 10px;
    color: #181818;
    text-align: center;
    font-family: "SF Pro Display", sans-serif;
    font-size: 28px;
    font-style: normal;
    font-weight: bold;
    line-height: normal;
}
.form h3{
    position: relative;
    top: -10px;
    color: var(--Gray-2, #4F4F4F);
    text-align: center;
    font-family: "SF Pro Display", sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    letter-spacing: 0.32px;
}
.form h4{
    color: #181818;
    font-family: "SF Pro Display", sans-serif;
    font-size: 14px;
    font-style:normal;
    font-weight: 550;
}

.emailcontent{
    margin-top: -10px;
    margin-bottom: 30px;
    display: flex;
    width: 412px;
    padding: 11px 12px 12px 12px;
    align-items: center;
    gap: 10px;
    border-radius: 4px;
    border-color: transparent;
    background: var(--Gray-6, #F2F2F2);
}
@media (max-width:500px) {
    .form{
        width: auto;
    }
    .emailcontent {
        width: 92%;
    }
}
.emailBtn{
    margin-bottom: 20px;
    width: 439px;
    border-radius: 6px;
    background: var(--Blue-1, #2F80ED);
    padding: 12px 18px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border: transparent;
    color: #FFF;
    font-family: "SF Pro Display", sans-serif;
    font-size: 15px;
    font-style: normal;
    transition: transform 1s ease-in-out;
}

.emailBtn:hover{
    border:  1px solid#2F80ED;
    background-color: white;
    color: #2F80ED;
    transform: scale(1.03);
    cursor: pointer;
}

@media (max-width:500px){
    .emailBtn{
        width: 98%;
    }
}

.form p{
    color: var(--Gray-2, #4F4F4F);
    font-family: "SF Pro Display", sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px; /* 128.571% */
    letter-spacing: 0.28px;
}

.form span{
    color: var(--Blue-1, #2F80ED);
    font-family: "SF Pro Display", sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0.28px;
    cursor: pointer;
}